.top-bar {
  padding-top: 25px;
  padding-bottom: 40px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include on-tablet {
    padding-top: 50px;
  }

  &__logo {
    display: flex;
    width: 64px;
    height: 46px;

    @include hover(transform, scale(1.2));

    @include on-tablet {
      width: 87px;
      height: 65px;
    }

  }

  &__icons {
    display: flex;
    gap: 24px;

    &-tooltip {
      position: relative;

      @include on-desktop {
        &:hover .icon__data {
          opacity: 1;
          pointer-events: all;
          cursor: pointer;
        }
      }
    }
  }
}
